body {
  font-family: "Helvetica", sans-serif !important;
  font-weight: 100;
  background: rgb(213, 229, 242);
  background: linear-gradient(
    4deg,
    rgba(213, 229, 242, 1) 0%,
    rgba(243, 216, 244, 1) 39%,
    rgba(249, 249, 249, 1) 65%,
    rgba(212, 224, 253, 1) 90%,
    rgba(235, 235, 254, 0.9934348739495799) 120%
  );
  transform: rotate(-0.25turn) translate(-90px, 0px) scale(0.8);
}

.bar {
  border-radius: 10px;
  display: block;
  clear: both;
  height: 30px;
  mix-blend-mode: lighten;
}

#empty {
  padding-left: 2vh;
  padding-right: 2vh;
  color: green;
  font-size: 3vw;
  font-weight: 150;
  display: flex;
  text-align: center;
  letter-spacing: 90%;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-items: center;
}

.number {
  transform: rotate(0.25turn);
  color: blueviolet;
  font-size: 3vw;
  display: flex;
  letter-spacing: 0.5vw;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-items: center;
}

.left {
  float: right;
  margin-right: 0;
}

.right {
  float: left;
  margin-left: 0;
  filter: hue-rotate(-210deg);
}

.left_container {
  opacity: 0.55;
  width: 45%;
  display: inline-block;
  height: 97vh;
  float: left;
  margin: 0px;
}

.middle_container {
  opacity: 0.93;
  width: 10%;
  height: 97vh;
  margin: auto;
  display: inline-block;
}

.right_container {
  mix-blend-mode: screen;

  opacity: 0.93;
  margin: 0px;
  width: 45%;
  height: 97vh;
  display: inline-block;
  float: right;
}
